import React from 'react';
import AttendanceButton from './AttendanceButton';
import FormField from './FormField';
import SleepButton from './SleepButton';
export default function PersonForm(props) {
  return (
    <div className="form-group person-form p-3 m-1">
      <AttendanceButton
        person={props.person}
        handleChange={props.handleChange}
        show={true}
      />
      <FormField
        id={props.person.id}
        type="text"
        name="name"
        className="form-control"
        placeholder="Förnamn..."
        value={props.person.name}
        handleChange={props.handleChange}
        show={true}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="surname"
        className="form-control"
        placeholder="Efternamn..."
        value={props.person.surname}
        handleChange={props.handleChange}
        show={true}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="email"
        className="form-control"
        placeholder="Email..."
        value={props.person.email}
        handleChange={props.handleChange}
        show={props.person.isAttending && props.person.id === 1}
      />

      <FormField
        id={props.person.id}
        type="text"
        name="diet"
        className="form-control"
        placeholder="Specialkost..."
        value={props.person.diet}
        handleChange={props.handleChange}
        show={props.person.isAttending}
      />
      <SleepButton
        person={props.person}
        handleChange={props.handleChange}
        show={props.person.isAttending}
      />
    </div>
  );
}
