import React, { Component } from 'react';

import SubmitButton from './SubmitButton';
import PersonForm from './PersonForm';
import AdditionalPersonsForm from './AdditionalPersonsForm';

const defaultState = {
  formIsValid: false,
  persons: [
    {
      isAttending: true,
      id: 1,
      name: '',
      surname: '',
      diet: '',
      sleepFri: false,
      sleepSat: false,
      email: ''
    }
  ]
};

export default class RSVPForm extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState;
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.addPerson = this.addPerson.bind(this);
    this.removePerson = this.removePerson.bind(this);
  }

  handleSubmit(event) {
    const scriptURL =
      'https://script.google.com/macros/s/AKfycbznWL2A-dRgzL-HGPDhYKjoA7UpAVCI6JHaiHRxNYIwE4UcgIg/exec';

    for (let i = 0; i < this.state.persons.length; i++) {
      let data = new FormData();
      for (let key in this.state.persons[i]) {
        data.append(key, this.state.persons[i][key]);
      }

      fetch(scriptURL, { method: 'POST', body: data })
        .then(response => console.log('Success!', response))
        .catch(error => console.error('Error!', error.message));
    }

    this.setState(defaultState);
    event.preventDefault();
  }

  handleChange(id, name, value) {
    console.log(id);
    console.log(name);
    console.log(value);
    this.setState({
      ...this.state,
      persons: this.state.persons.map(person => {
        if (id === person.id) {
          return {
            ...person,
            [name]: value
          };
        } else {
          return person;
        }
      })
    });
  }
  checkForm() {}

  addPerson() {
    this.setState({
      ...this.state,
      persons: [
        ...this.state.persons,
        {
          ...defaultState.persons[0],
          id: this.state.persons.length + 1
        }
      ]
    });
  }

  removePerson() {
    if (this.state.persons.length > 1) {
      this.setState({
        ...this.state,
        persons: this.state.persons.filter(
          person => person.id !== this.state.persons.length
        )
      });
    } else {
      this.setState(defaultState);
    }
  }

  render() {
    return (
      <div>
        <section id={this.props.section} className="sec p-4 m-3">
          <h3 className="text-center mb-4">Anmäl dig!</h3>
          <form onSubmit={this.handleSubmit}>
            <div className="row justify-content-md-center">
              <div className="col-md-6">
                {this.state.persons.map(person => (
                  <PersonForm
                    key={person.id}
                    handleChange={this.handleChange}
                    person={person}
                  />
                ))}

                <AdditionalPersonsForm
                  addPerson={this.addPerson}
                  removePerson={this.removePerson}
                />
                <SubmitButton />
              </div>
            </div>
          </form>
        </section>
      </div>
    );
  }
}
